.hero {
  padding-top: 136px;
  padding-bottom: 158px;

  background-color: var(--product-pink-color);

  &__back {
    display: block;
    //

    width: 345px;
    height: 345px;
    top: 83px;
    left: 20px;

    background: var(--light-pink-color);
    border-radius: 50%;
    position: absolute;
    transform: translateY(-50%);
  }
}

.hero > .container {
  background: var(--product-pink-color);
  margin-left: 20px;
}

.hero__button {
  display: flex;
  z-index: 1;
  position: relative;
}

.hero__title {
  max-width: 218px;

  position: relative;
  z-index: 1;
  font-family: 'Titan One';
  font-weight: 400;
  font-size: 26px;
  line-height: 1.15;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  margin-bottom: 20px;

  color: var(--primary-white-color);
}

.hero__title--accent {
  font-size: 48px;
  line-height: 0.96;

  color: var(--dark-pink-color);
}

.hero__link {
  display: inline-block;
  padding: 10px 22px;
  margin-right: 8px;

  font-family: DM Sans;
  font-weight: 400;
  font-size: 8px;
  line-height: 1.25e;

  color: var(--primary-white-color);
  background-color: var(--dark-pink-color);
  border-radius: 20px;
}

.hero__link--invers {
  color: var(--dark-pink-color);
  background-color: var(--primary-white-color);
  margin-right: 0px;
}
