
.visually-hidden {
    position: absolute;
    white-space: nowrap;
    width: 1px;
    height: 1px;
    overflow: hidden;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    clip-path: inset(50%);
    margin: -1px;
}
.contacts__list {
    margin-bottom: -20px;
}
.card {
    padding: 40px 32px;
    width: 280px;
    margin-bottom: 20px;
    background: var(--primary-white-color);
    box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
    border-radius: 24px;
}
.card__cafe {
    font-size: 12px;
    line-height: 1.86;
    width: 80px;
    height: 16px;
    margin-bottom: 20px;
    margin-top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--product-creamy-color);
    border-radius: 4px;
    letter-spacing: 0.04em;
    color: var(--primary-white-color);
}
.card__cafe--foodtruck {
    width: 130px;
    background: var(--product-green-color);
}
.card__title {
    font-size: 12px;
    line-height: 1.86;
    letter-spacing: 0.04em;
    margin-top: 0;
    margin-bottom: 8px;
    color: var(--primary-black-color);
}
.card__text {
    font-size: 12px;
    line-height: 1.5;
    margin: 0;
    padding-bottom: 24px;
    color: var(--text-gray-color);
    border-bottom: 1px solid #E1E1E1;
}
.card__list {
    padding-top: 24px;
    padding-bottom: 24px;
    margin-bottom: 24px;
    border-bottom: 1px solid #E1E1E1;
}
.card__item + .card__item {
    margin-top: 8px;
}
.card__days {
    font-size: 12px;
    line-height: 1.86;
    margin: 0;
    color: var(--primary-black-color);
}
.card__time {
    font-size: 12px;
    line-height: 1.86;
    margin: 0;
    color: var(--light-pink-color);
}
.card__link {
    display: block;
    font-size: 12px;
    line-height: 1.5;
    color: var(--text-gray-color);
}
.contacts__button {
    display: flex;
}
